<template>
    <div>
        <v-container grid-list-lg text-xs-center>
        </v-container>
    </div>
</template>

<script>
import { mapActions, mapGetters } from "vuex";

export default {
  components: {},
  computed: {
    success: {
      get: function() {
        return this.$store.state.status.success;
      },
      set: function(value) {
        this.$store.dispatch("clearError");
      }
    },
    error: {
      get: function() {
        return this.$store.state.status.error;
      },
      set: function(value) {
        this.$store.dispatch("clearError");
      }
    },
    ...mapGetters(["skills", "loading"])
  },
  watch: {
    page: "getSkills"
  },
  methods: {
    getSkills() {
      this.$store.dispatch("getSkills");
    }
  }
};
</script>

<style lang="stylus" scoped>
#keep {
    .card__title {
        display: block;
    }

    .headline {
        a {
            color: rgba(0, 0, 0, 0.87);
            text-decoration: none;
        }
    }

    .v-spinner {
        text-align: center;
    }

    .noRecipes {
        position: absolute;
        right: 0;
        left: 0;
        text-align: center;
        height: auto;
        top: 60px;
        bottom: 0;
    }

    .recipes-pagination {
        margin-top: 80px;
    }

    .pagination >>> li > a.pagination__item--active {
        background: #FFC107;
        color: #000;
        font-weight: bold;
    }
}
</style>


